.cta {
  color: #fff;
  background: #ff7300;
  border-radius: 50px;
  margin-top: 245px;
  padding: 10px 45px;
  font-size: 40px;
  text-decoration: none;
  transition: all 1s;
  display: flex;
  transform: skewX(-10deg);
  box-shadow: 6px 6px #2a2a2a;
}

.cta:focus {
  outline: none;
}

.cta:hover {
  transition: all .5s;
  box-shadow: 10px 10px .5px #fff;
}

.cta .cta-span:nth-child(2) {
  margin-right: 0;
  transition: all .5s;
}

.cta:hover .cta-span:nth-child(2) {
  margin-right: 45px;
  transition: all .5s;
}

.cta-span {
  transform: skewX(10deg);
}

.cta-span:nth-child(2) {
  width: 20px;
  margin-left: 30px;
  position: relative;
  top: 12%;
}

path.one {
  transition: all .4s;
  transform: translateX(-60%);
}

path.two {
  transition: all .5s;
  transform: translateX(-30%);
}

.cta:hover path.three {
  animation: 1s .2s infinite color_anim;
}

.cta:hover path.one {
  animation: 1s .6s infinite color_anim;
  transform: translateX(0%);
}

.cta:hover path.two {
  animation: 1s .4s infinite color_anim;
  transform: translateX(0%);
}

@keyframes color_anim {
  0% {
    fill: #fff;
  }

  50% {
    fill: #595959;
  }

  100% {
    fill: #fff;
  }
}

